:root{
  --main-color: #ffffff;
  --main-bg-color: #EF6720;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}
.container{
  display: flex;
  align-items: center;
  height: auto;
  width: auto;
}
.slider-track{
  display: block;
  position: relative;
  width: 60vw;
  overflow: hidden;
}

.slider-btn{
  z-index: 1;
  height: 10em;
  width: 0;
}
.container:hover .slider-btn{
  box-shadow: 20px -12px 40px 60px #ffffff;
  border: none;
  z-index: 1;
}

.slider-btn button{
  border-radius: 50px;
  border: 1px #ece8e6 solid;
  height: 40px;
  width: 40px;
  padding: 0.6em;
  color: #868686;
  background: #ffffff;
}

.container #prev{
  transition: visibility 0.5s;
}
.container #next{
  position: relative;
  left: -3.5vw;
  transition: visibility 0.5s;
}
.card-show-container{
  display: flex;
  justify-content: center;

  background-color: var(--main-bg-color);
  height: 10vh;
  width: 100%;
}
.card-show-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--main-color);
}
.card-show-wrapper i{
  font-size: 30px;
}
.hidden{
  visibility: hidden;
}






